  z-index: 100;
  height: 15px;
  width: 150px;
  margin-left: auto;
  margin-right: auto;
  position: fixed;
  top: 250px;
  left: 0;
  right: 0;
}

.hollow-dots-spinner .dot {
  width: 25px;
  height: 25px;
  float: left;
  background-color: #fff;
  border: 3px solid #ff6b08;
  border-radius: 50%;
  margin: 0 12.5px;
  animation: hollow-dots 1s infinite;
  transform: scale(0);
}

.hollow-dots-spinner .dot:nth-child(1) {
  animation-delay: .3s;
}

.hollow-dots-spinner .dot:nth-child(2) {
  animation-delay: .6s;
}

.hollow-dots-spinner .dot:nth-child(3) {
  animation-delay: .9s;
}

@keyframes hollow-dots {
  50% {
    opacity: 1;
    transform: scale(1);
  }

  100% {
    opacity: 0;
  }
}

